<template>
    <b-modal
      variant="dark"
      header-bg-variant="dark"
      header-text-variant="light"
      content-class="border-dark"
      v-model="showModal"
    >
      <!-- title -->
      <template v-slot:modal-title>
        {{ data.title }}
      </template>
      <!-- body -->
      <template v-slot:default>
        <div class="modal_body">
          <p>{{ data.text }}</p>
        </div>
      </template>
      <!-- footer -->
      <template v-slot:modal-footer>
        <b-button
          v-if="data.confirm"
          variant="primary"
          @click="popupSubmit()"
        >
          {{ $t('common.ok') }}
        </b-button>
        <b-button
          v-if="data.confirm"
          variant="dark"
          @click="hide()"
        >
          {{ $t('common.cancel') }}
        </b-button>
        <b-button
          v-if="!data.confirm"
          variant="primary"
          @click="hide()"
        >
          {{ $t('common.ok') }}
        </b-button>
      </template>
    </b-modal>
</template>

<script>
export default {
  name: 'successModal',
  data () {
    return {
      showModal: false,
      data: {
        title: '',
        text: '',
        confirm: false
      }
    }
  },
  methods: {
    show (_data) {
      this.data = {
        title: _data.title,
        text: _data.text,
        confirm: _data.confirm
      }
      this.showModal = true
    },
    hide () {
      this.showModal = false
    },
    popupSubmit () {
      this.$emit('submit', this.data)
    }
  }
}
</script>

<style scoped>
.close {
  color: #fff !important;
  opacity: 1 !important;
}
.modal_body{
  min-height: 100px;
}
.modal_body > p{
  font-size: 15px;
  font-weight: 700;
  padding-top: 30px;
}
</style>
